import { LoginEntrena } from './../../Interfaces/loginEntrena.interface';
import { PlatformsService } from './../../Services/PLatforms/Platform.service';
import { StorageWebService } from './../../Services/Storage/StorageWeb.service';
import { Component, OnInit } from '@angular/core';
import { ApiService } from '../../Services/Api/Api.service';
import { Login } from '../../Interfaces/Login.interface';
import { ToastService } from '../../Services/Utilities/Toast.service';
import { NavController, MenuController } from '@ionic/angular';
import { DataComparnyService } from 'src/app/Services/dataCompany.service';
import { Router } from '@angular/router';
import { Socket } from 'ngx-socket-io';
import { Storage } from '@ionic/storage-angular';
import { trigger, state, style, transition, animate } from '@angular/animations';
import { ApiEntrenaService } from 'src/app/Services/Api/ApiEntrena.service';

@Component({
  selector: 'app-login',
  templateUrl: './login.page.html',
  styleUrls: ['./login.page.scss'],
  animations: [
    trigger('openClosed', [
      transition(':enter', [
        style({
          opacity: 0,
          transform: 'scale(0)'
        }),
        animate('500ms', style({
          opacity: 1,
          transform: 'scale(1)'
        }))
      ]),
      transition(':leave', [
        style({
          opacity: 1,
          transform: 'scale(1)'
        }),
        animate('0ms', style({
          opacity: 0,
          transform: 'scale(0)'
        }))
      ])
    ])
  ]
})
export class LoginPage implements OnInit {

  login: Login = {
    user: '',
    password: ''
  };

  loginEntrena: LoginEntrena = {
    login: '',
    password: ''
  };

  typeApp = '2';

  statusResponse = false;
  plt = '';

  ext = false;

  IPAddres = '';

  constructor(
    private apiService: ApiService,
    private toastService: ToastService,
    private navCtrl: NavController,
    private dataCompanyService: DataComparnyService,

    private storageService: StorageWebService,
    private myPlatform: PlatformsService,
    private menuCtrl: MenuController,
    private router: Router,
    private socket: Socket,
    private storage: Storage,
    private VTEntrenaApi: ApiEntrenaService
    ) {

      this.myPlatform.$myplatform.subscribe((plt: any) => {
        this.plt = plt;
      });
    }

  ngOnInit() {
  }

  ionViewWillEnter() {
    this.menuCtrl.enable(false, 'menu');
    this.createdCollections();
    this.VTEntrenaApi.readIP().subscribe((rp: any) => {
      if (rp.ip) {
        this.IPAddres = rp.ip;
      }
    })
    
  }

  async createdCollections() {
    console.log('Holaaa')
    const login = await this.storage.get('login');
    const forms = await this.storage.get('forms');
    const formsActive = await this.storage.get('formActive');
    const chats = await this.storage.get('chats');

    if (!login) {
      await this.storage.set('login', []);
    } 

    if (!forms) {
      await this.storage.set('forms', []);
    }

    if (!formsActive) {
      await this.storage.set('formActive', []);
    }

    if (!chats) {
      await this.storage.set('chats', []);
    }

  }

  changeApp( type: string ) {
    if (type === '1' || type === '2') {
      this.typeApp = type;
    }
  }

  changeLogin( event ) {
    this.ext = event.detail.checked;
    console.log(event)
  }

  sessionInit() {
   if (this.typeApp === '1') {
    if (this.ext) {
      this.statusResponse = true;
      this.apiService.postLoginExte(this.login).subscribe((data: any) => {
            console.log(data);
  
            if (data.status === 'error') {
              this.statusResponse = false;
              this.toastService.newCreatedToast('No se pudo iniciar sesion, intentelo mas tarde', false);
            } else if (data.status === 'ok' && data.response === '') {
              this.statusResponse = false;
              this.toastService.newCreatedToast('Credenciales Incorrectas, compruebe su informacion', false);
            } else if (data.status === 'ok' && data.response.length > 0) {
              this.dataCompanyService.getDataCompany({
                pathLogoCompany: 'https://s3.amazonaws.com/logocompanies/D0DE636B-6CED-41BB-BA27-6ED7A1AE771D.jpg',
                nameCompany: 'Visitrack',
                ext: true
              });
  
              this.socket.emit('login', {ID: data.response[0].login});
              this.socket.emit('welcomeExt', {
                CompanyID:  data.response[0].idCompany,
                name:  data.response[0].name,
                login:  data.response[0].login,
                type: 'exter'
              });
              if (this.plt === 'web') {
                this.storageService.insertUser({
                  CompanyID:  data.response[0].idCompany,
                  name:  data.response[0].name,
                  login:  data.response[0].login,
                  type: 'exter',
                  logo: 'https://s3.amazonaws.com/logocompanies/D0DE636B-6CED-41BB-BA27-6ED7A1AE771D.jpg',
                  companyName: 'Visitrack Application'
                });
                this.menuCtrl.enable(true, 'menu')
                this.toastService.newCreatedToast('Iniciando', true).then(() => {
                  this.router.navigate(['/home']);
                  
                });
              }
  
              console.log(data.response);
    
              this.statusResponse = false;
            }
          
    
      }, (err) => {
          this.statusResponse = false;
          this.toastService.newCreatedToast(err, true);
      });
     } else {
      this.statusResponse = true;
      this.apiService.postLogin(this.login).subscribe((data: any) => {
  
          data = JSON.parse(data);
          console.log(data);
          delete data.Password;
          data.password = this.login.password;
          data.login = this.login.user;
          data.name = data.FirstName + ' ' + data.LastName;
  
          
  
          console.log(data, 'datica');
  
          if (typeof data.AccessToken === 'undefined') {
              console.log('Error ', data.errorMsg);
              this.statusResponse = false;
              this.toastService.newCreatedToast(data.errorMsg, false);
          } else {
  
            this.apiService.getUsersCompany({AccessToken: data.AccessToken}).subscribe((dat: any) => {
    
              dat = JSON.parse(dat);
      
              const user = dat[0].filter((item) => {
                return item.ID === data.UserID;
              });
  
              data.ROLE = user[0].RoleCode;
              data.type = 'inter';
  
              this.socket.emit('login', {ID: data.UserID});
              this.socket.emit('welcome', data);
              this.statusResponse = false;
              this.dataCompanyService.getDataCompany({
                pathLogoCompany: data.logo,
                nameCompany: data.companyName,
                ext: false
              });
  
              if (this.plt === 'web') {
                this.storageService.insertUser(data);
                this.menuCtrl.enable(true, 'menu')
                this.toastService.newCreatedToast('Iniciando', true).then(() => {
                  this.router.navigate(['/forms']);
                  
                });
              }
            });
  
          }
      }, (err) => {
          this.statusResponse = false;
          this.toastService.newCreatedToast(err, true);
      });
     }
   } else if (this.typeApp === '2') {
      const navegadores = ['Edg', 'Chrome', 'Safari', 'Firefox', 'Opera', 'MSIE', 'Trident'];
      const agente = navigator.userAgent;
      let ready = false;
      let naveName = '';
      navegadores.forEach(nave => {
        
        if (agente.indexOf(nave) !== -1 && !ready) {
          naveName = nave;
          ready = true;
        }
      })
      // tslint:disable-next-line:max-line-length
      this.VTEntrenaApi.loginUser(this.loginEntrena, this.IPAddres, {name: naveName, agent: navigator.userAgent}).subscribe(async (res: any) => {
        console.log(res);
        if (res.status === 'ok') {

          res.response.typeApp = '2';
      

          this.dataCompanyService.getDataCompany({
            logo: res.response.data.logo,
            name: res.response.data.name,
          });


          this.storageService.insertUser(res.response).then(() => {
            this.menuCtrl.enable(true, 'menu');
            this.router.navigate(['/list-my-designs']);
            this.socket.emit('connectVT', res.response.data)
            this.dataCompanyService.getVista('3');
          });

        } else {
          this.toastService.newCreatedToast(`${res.response}`, false);
        }
      }, (error) => {
        this.toastService.newCreatedToast(`Error session ${error}`, false);
      })
   }
  }

}
